import { ProductItem, ProductItemDeprecated } from 'store/constants/products';

// FIXME: https://sbtatlas.sigma.sbrf.ru/jira/browse/FOODTECHUI-356
export function isProductItemDeprecated(
    productItem: ProductItemDeprecated | ProductItem,
): productItem is ProductItemDeprecated {
    return Boolean('id' in productItem) === false;
}
