import { useSelector } from 'hooks/useSelector';
import { selectHeader } from 'store/selectors';

export function useIsHeaderVisible() {
    return useSelector((state) => selectHeader(state).isVisible);
}

export function useHeaderAddressVisible() {
    return useSelector((state) => selectHeader(state).address.isVisible);
}

export function useHeaderCartVisible() {
    return useSelector((state) => selectHeader(state).cart.isVisible);
}

export function useIsBackButtonVisible() {
    return useSelector((state) => selectHeader(state).isBackButtonVisible);
}

export function useTitle() {
    return useSelector((state) => selectHeader(state).title);
}

export function useSubtitle() {
    return useSelector((state) => selectHeader(state).subtitle);
}
