import React from 'react';
import styled from 'styled-components';
import { useContainerFocus } from 'hooks/useContainerFocus';
import { layoutHeaderHeightVar, layoutPaddingSide } from 'components/GlobalLayoutVars/GlobalLayoutVars';
import { HeaderAddress } from 'components/HeaderAddress/HeaderAddress';
import { HeaderCart } from 'components/HeaderCart/HeaderCart';
import { transition } from 'style/const/transition';

import { HeaderTitle } from './Title/HeaderTitle';
import {
    useHeaderAddressVisible,
    useHeaderCartVisible,
    useIsBackButtonVisible,
    useIsHeaderVisible,
} from './Header.hooks';

interface StyledHeaderProps {
    isVisible: boolean;
}

export const StyledHeader = styled.div<StyledHeaderProps>`
    --spatial-navigation-contain: contain;

    position: relative;
    z-index: 3;

    height: var(${layoutHeaderHeightVar});
    padding: 52px 88px 0 var(${layoutPaddingSide});

    box-sizing: border-box;

    display: flex;
    justify-content: space-between;
    align-items: center;

    opacity: ${({ isVisible }) => Number(isVisible)};

    transition: opacity ${transition.default};
`;

const StyledSection = styled.div`
    display: flex;
    align-items: center;
`;

const cartButtonRef = React.createRef<HTMLButtonElement>();

interface HeaderCommonProps {
    renderBackButton?: () => React.ReactNode;
}

export const HeaderCommon: React.FC<HeaderCommonProps> = ({ renderBackButton, ...props }) => {
    const isHeaderVisible = useIsHeaderVisible();
    const isAddressVisible = useHeaderAddressVisible();
    const isCartVisible = useHeaderCartVisible();
    const isBackButtonVisible = useIsBackButtonVisible();

    const handleReceiveFocus = React.useCallback(() => {
        if (isCartVisible) {
            cartButtonRef.current?.focus();
        }
    }, [isCartVisible]);

    const { handleFocus, handleBlur } = useContainerFocus({
        onReceiveFocus: handleReceiveFocus,
    });

    return (
        <StyledHeader isVisible={isHeaderVisible} onFocus={handleFocus} onBlur={handleBlur} {...props}>
            {isBackButtonVisible && renderBackButton?.()}
            <StyledSection>
                <HeaderTitle />
            </StyledSection>
            <StyledSection>
                <HeaderAddress isVisible={isAddressVisible} />
                <HeaderCart forwardRef={cartButtonRef} isVisible={isCartVisible} />
            </StyledSection>
        </StyledHeader>
    );
};
