import React from 'react';
import styled from 'styled-components';
import { push } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { selectHeader } from 'store/selectors';
import { ROUTES } from 'store/constants/router';
import { HeaderButtonTouch } from 'components/HeaderButton/HeaderButton@touch';
import { icon } from 'style/const/icon';

import { ReactComponent as AddressSvg } from './address.svg';

const addressIconSizePx = icon.xs.sizePx;

const StyledIcon = styled(AddressSvg)`
    width: ${addressIconSizePx}px;
    height: ${addressIconSizePx}px;
`;

export const HeaderAddressTouch: React.FC = () => {
    const dispatch = useDispatch();

    const { isVisible } = useSelector((state) => selectHeader(state).address);

    const handleClick = React.useCallback(() => dispatch(push(ROUTES.ADDRESS_SIMPLE)), [dispatch]);

    return (
        <HeaderButtonTouch isVisible={isVisible} onClick={handleClick}>
            <StyledIcon />
        </HeaderButtonTouch>
    );
};
